<template>
  <div class="mainDiv" :style="backgroundPage">
      <b-container >
            <b-row class="text-center darkText">
                <b-col>
                    <div class="aboutDiv darkText topMargin">
                        EXPERIENCES
                    </div>
                </b-col>              
            </b-row>
            <b-row class="tab-container">
                <b-col cols="12" md="3">
                    <div class="tabs">
                        <div v-for="(item,index) in content" :key=item.tab 
                            class="tab" 
                            v-on:click="handleTabOnClick(index)">
                            {{item.tab}}
                            <div v-if="index == selectedItem" class="indicator">
                            </div>
                        </div>
                    </div>
                </b-col>
                <b-col cols="12" md="9" class="content" :key="selectedItem">
                    <div style="margin:10px 0px;">
                        <img class="imgIcon" :src="content[selectedItem].asset"/>
                    </div>
                    <div>
                        <span class="was"> I was a : </span> {{content[selectedItem].title}}
                    </div>
                    <div class="divider"/>
                    <div style="margin:10px 0px">
                        {{content[selectedItem].content}}
                    </div>
                </b-col>
            </b-row>   
      </b-container>
  </div>
</template>

<script>
import {colors} from '../constants/theme';
import universityLogo from '../assets/university.svg';
import internshipLogo from '../assets/internship.svg';
import engineerLogo from '../assets/engineer.svg';

export default {

    mounted(){
        this.selectedItem = this.content.length - 1;
    },
    data(){
        return{
            backgroundPage:{
                background:colors.lightPrimary               
            },
            cardCols:3,
            content:[
                {
                    tab:'2016 - NTU',
                    content:'Studied Computer Science and graduated with a Second Lowers honours, and specialized in Artificial Intelligence',
                    title:'Student',
                    asset:universityLogo
                },
                {
                    tab:'2019 - Thermo Fisher Scientific',
                    content:'Helped to create test units for web applications using Selenium testing framework. Also worked on a facial unlock POC for their PCR machine based on Android',
                    title:'Intern',
                    asset:internshipLogo
                },
                {
                    tab:'2020 - CSIT',
                    content:'Worked on web applications using VueJS,ReactJS,Spring Boot and SQL',
                    title:'Software Engineer',
                    asset:engineerLogo

                }
            ],
            selectedItem:0
        }
    },
    methods:{
        handleTabOnClick(index){
            this.selectedItem = index;
        }
    }

}
</script>

<style>

.divider{
    width:100%;
    height:1px;
    background-color: #eaeaea;
    margin: 10px 0px;
}

.tab-container{
    background-color: #f9f9f9;
    border-radius: 4px 4px 36px 4px;
    border: solid;
    border-color: #e1e1e1;
    border-width: 1px;
    padding-left: 15px;
    padding-right: 15px;
    box-shadow: 0px 3px 1px 1px #0000000a;

    font-family: Segoe UI;
    color: #373737;
}

.tabs{
    border: solid;
    border-width: 0px;
    border-right-width: 1px;
    border-color: #bdbdbd;
}

.was{
    font-size: 0.8rem;
    font-style: italic;
}

.not-active{
    font-size: 0.9em;
    color: #7b7b7b;
}

.tab{
    padding: 15px 20px;
    margin-left: -30px; 
    transition:background-color 0.2s;
    position:relative;
    display:flex;
    align-items:center;
}

.indicator{
    position: absolute;
    right: 0px;
    background-color: #78b7b0;
    height: 40px;
    width: 3px;
    transition:0.2s;
}

.tab:hover{
    background-color: rgba(173, 173, 173, 0.301);
    cursor: pointer;
}

.tab:hover .indicator{
    height:50px;
    width:2px;
}

@keyframes fadeIn {
  from {opacity: 0%;}
  to {opacity: 100%;}
}

.content{
    animation-name:fadeIn;
    animation-duration: 0.6s;
    padding: 10px;
    display: flex;    
    flex-direction: column;
}

.line{
    margin-top:10px;
    margin-bottom:2px;
    height:100px;
    border-width: 0px 0px 0px 2px;
    border-style: dashed;
    border-color: #a0a0a0;
}

.arrow-down{
    margin-bottom:8px;
    background-color: #a0a0a0;
    clip-path: polygon(0 0, 50% 100%, 100% 0, 50% 21%);
    height:10px;
    width:10px;
}

.imgIcon{
    height:50px;
    width:50px;
}


#svg{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#line{
  stroke-width:2px;
  stroke:rgb(0,0,0);
}

.point{
  width: 10px;
  height: 10px;
  background: red;
  position: absolute;
}

#one{
  top: 20px;
  left: 50px;
}

#two{
  bottom: 20px;
  right: 50px;
}

.bottomMargin{
    margin-bottom: 10px;
}

.flexCenter{
    display:flex;
    align-items: center;
    flex-direction: column;
    
}

.wrap{
    background-color: #f3f3f3 !important;
    padding:30px;
    border-radius: 8px;
    min-width: 400px;
 }


.list{
    width:90%;
    text-align:left;
}

@media only screen and (max-width:800px) {
    .list{
        width:80%;
    }
    .wrap{
        max-width: 90%;
        padding:15px;
        min-width: 90%;
    }
    .line{
        height:80px;
    }
}


</style>